import {RouterModule} from '@angular/router';


import{
  ClienteMainComponent,
  ListaClienteComponent
} from './'

const routes = [{
  path: '',
  component: ClienteMainComponent,
  children: [{
    path: 'lista',
    component: ListaClienteComponent
  }]
}]

export const ClienteRoutes = RouterModule.forChild(routes)



