import { Component, ViewChild } from '@angular/core'
import { Router, ActivatedRoute } from '@angular/router'
import {ClientService, UtilisService} from '../../../services'

@Component({
  moduleId: module.id,
  templateUrl: 'registrar.component.html',
  styleUrls: ['registrar.component.scss']
})
export class RegistrarClienteComponent {
  cliente
  entrega
  editar
  estados$
  ciudades$
  estados2$
  ciudades2$
  sucursales = []
  titulo = 'Registrar'

  constructor(
    private clientService: ClientService,
    private router: Router,
    private route: ActivatedRoute,
    private utilsService: UtilisService
  ) {
    const {url} = router
    this.checkIfUpdate(url)
  }

  ngOnInit() {
    this.cleanCliente()
    this.cleanEntrega()
    this.estados$ = this.utilsService.getEstados()
    this.estados2$ = this.utilsService.getEstados()
  }

  getCiudades (estado) {
    this.ciudades$ = this.utilsService.getCiudades(estado)
  }

  getCiudades2 (estado) {
    this.ciudades2$ = this.utilsService.getCiudades(estado)
  }

  checkIfUpdate (url) {
    if (/editar/.test(url)) {
      this.editar = true
      this.clientService.getClient(this.route.snapshot.params['id'])
        .subscribe(data => {
          this.titulo = 'Editar'
          this.sucursales = data.entrega.slice()
          delete data.entrega
          this.cliente = data
          this.cleanEntrega()
        })
    }
  }

  createClient() {
    if (this.entrega.address !== '') {
      this.sucursales.push(this.entrega)
    }

    this.cliente.entrega = [...this.sucursales]

    if (this.editar) {
      this.clientService
        .updateClient({cliente: this.cliente})
        .subscribe(() => this.redirect())
    } else {
      this.clientService
        .createClient({cliente: this.cliente})
        .subscribe(() => this.redirect())
    }
  }

  addSucursal() {
    if (this.entrega.address !== '') {
      this.sucursales.push(this.entrega)
      this.cleanEntrega()
    }
  }

  deleteSucursal (index) {
    this.sucursales.splice(index, 1)
  }

  redirect () {
    this.cleanCliente()
    this.cleanEntrega()
    this.router.navigate(['user', 'clientes', 'lista'])
  }

  cleanCliente () {
    this.cliente = {
      "code": "",
      "name": "",
      "state": "seleccione un estado",
      "rfc": "",
      "address": "",
      "cp": "",
      "city": "seleccione una ciudad",
      "colonia": "",
      "email": "",
      "phone": "",
      entrega: []
    }
  }

  cleanEntrega () {
    this.entrega = {
      "name": "",
      "cp": "",
      "address": "",
      "state": "seleccione un estado",
      "colonia": "",
      "city": "seleccione una ciudad"
    }
  }
}



